import React, { Component } from "react";
import {
  StyleSheet,
  Dimensions,
  Text,
  View,
  ScrollView,
  Animated
} from "react-native";
import movieList from "../mockdata/data";

const { width, height } = Dimensions.get("window");

export default class Home extends Component {
  render() {
    return (
      <View style={styles.container}>
        <Animated.ScrollView
          pagingEnabled
          scrollEventThrottle={16}
          horizontal
          onScroll={Animated.event(
            [{ nativeEvent: { contentOffset: { x: this._scrollX } } }],
            { useNativeDriver: true }
          )}
          contentContainerStyle={styles.scrollViewContainier}
        >
          {movieList.map((item, i) => this._renderMovie(item, i))}
        </Animated.ScrollView>
      </View>
    );
  }
  _renderMovie = (item, i) => {
    return (
      <View key={item.id} style={[styles.container, styles.item]}>
        <Text style={[styles.font, styles.title]}>{item.title}</Text>
        <Text style={[styles.font, styles.rating]}>{item.rating}</Text>
        <Text style={[styles.font, styles.runtimeMinutes]}>
          {item.runtimeMinutes}
        </Text>
        <Text style={[styles.font, styles.averageRating]}>
          {item.averageRating}
        </Text>
      </View>
    );
  };
  _scrollX = new Animated.Value(0);
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center"
  },
  scrollViewContainier: {
    alignItems: "center",
    justifyContent: "center"
  },
  item: {
    width,
    height,
    alignItems: "center"
  },
  font: {
    fontFamily: "monospace",
    color: "#222"
  },
  metaContainer: {
    alignItems: "center",
    justifyContent: "flex-end",
    backgroundColor: "transparent",
    padding: 15
  },
  title: {
    fontSize: 36,
    fontWeight: "700"
  },
  rating: {
    fontSize: 14,
    marginVertical: 15,
    textAlign: "center"
  },
  runtimeMinutes: {
    fontSize: 14,
    marginVertical: 15,
    textAlign: "center"
  },
  averageRating: {
    fontSize: 24,
    fontWeight: "900"
  }
});
